@import url("https://fonts.googleapis.com/css2?family=Orbitron:wght@400;500;600;700;800;900&display=swap");
html > body {
  background-color: #000000;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  height: 100vh;
  width: 100vw;
  margin: 0 auto;
  padding: 0;
  font-family: 'Orbitron', sans-serif;
}

.container {
  text-align: center;
  padding: 15px;
}

.header-title {
  font-family: 'Orbitron', sans-serif;
  color: #FFE81F;
  text-transform: uppercase;
  letter-spacing: 0.1em;
  font-size: 3em;
  margin-bottom: 40px;
}

.ui.card, .ui.cards > .card {
  width: 90%;
  background: #4F4F4F;
  border-radius: 20px;
}

.card-header {
  font-family: 'Orbitron', sans-serif;
  color: #FFFFFF;
  padding: 15px 5px;
  text-align: center;
  font-weight: bold;
  text-transform: uppercase;
  letter-spacing: 0.1em;
}

h3.title-strong {
  font-family: 'Orbitron', sans-serif;
  padding: 5px 10px;
  text-transform: uppercase;
  color: #000000;
  font-size: 1.3em;
  font-weight: bold;
}

p.title-reg {
  padding: 2px 10px;
  color: #FFE81F;
  font-size: 1em;
  font-style: italic;
  text-transform: uppercase;
}

.ui.card > .content > .description, .ui.cards > .card > .content > .description {
  padding: 10px 0 30px 10px;
}

ul {
  -webkit-padding-start: 0;
          padding-inline-start: 0;
}

.paginationBttns {
  width: 100%;
  height: 40px;
  list-style: none;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  margin-top: 30px;
}

.paginationBttns a {
  padding: 10px;
  margin: 8px;
  border-radius: 5px;
  border: 1px solid #FFFFFF;
  color: #FFFFFF;
  cursor: pointer;
}

.paginationBttns a:hover {
  color: #000000;
  background-color: #FFE81F;
}

.paginationActive a {
  color: #FFFFFF;
  background-color: #FFE81F;
}

.paginationDisabled a {
  color: #C9C9C9;
  background-color: #4F4F4F;
}

.paginationDisabled a:hover {
  color: #C9C9C9;
  background-color: #4F4F4F;
  cursor: default;
}
